	module.exports = {"data":{"challengeNode":{"title":"Problem 75: Singular integer right triangles","description":["It turns out that 12 cm is the smallest length of wire that can be bent to form an integer sided right angle triangle in exactly one way, but there are many more examples.","12 cm: (3,4,5)24 cm: (6,8,10)30 cm: (5,12,13)36 cm: (9,12,15)40 cm: (8,15,17)48 cm: (12,16,20)","In contrast, some lengths of wire, like 20 cm, cannot be bent to form an integer sided right angle triangle, and other lengths allow more than one solution to be found; for example, using 120 cm it is possible to form exactly three different integer sided right angle triangles.","120 cm: (30,40,50), (20,48,52), (24,45,51)","Given that L is the length of the wire, for how many values of L ≤ 1,500,000 can exactly one integer sided right angle triangle be formed?"],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-75-singular-integer-right-triangles","blockName":"Project Euler","tests":[{"text":"<code>euler75()</code> should return 161667.","testString":"assert.strictEqual(euler75(), 161667, '<code>euler75()</code> should return 161667.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler75() {\n  // Good luck!\n  return true;\n}\n\neuler75();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-76-counting-summations","id":"5900f3b71000cf542c50feca"},"slug":"/coding-interview-prep/project-euler/problem-75-singular-integer-right-triangles"}}

